import React, { useEffect, useState } from 'react';
import first_popup_screen from '../assets/image/popup_1.png';
import Clear from '../assets/image/Clear.png';
import styled from "styled-components";
import { useNavigate } from 'react-router-dom';

        const ModalContainer = styled.div`
            display: flex;
            justify-content: center;
            align-items: center;
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.5);
        `;

        const ModalContent = styled.div`
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 342px;
            height: 436px;
            z-index: 2;
        `;

        const PopupImage = styled.img`
            width: 100%;
            height: 100%;
            object-fit: contain;
        `;

        const ClearButton = styled.img`
            position: absolute;
            top: -35px;
            right: 0;
            width: 26px;
            height: 26px;
            cursor: pointer;
            z-index: 3;
        `;

        const Background = styled.div`
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.5);
            z-index: 100;
        `;

    function Modal() {
            const [showModal, setShowModal] = useState(false);
            const navigate = useNavigate();

        useEffect(() => {
            setShowModal(true);
        }, []);

        const handleCloseModal = () => {
            setShowModal(false);
            console.log("x-click");
        };

        const go_Popup = () => {
            setShowModal(false);
            navigate('/popup');
        };

        const handleModalContentClick = (e) => {
            e.stopPropagation();
            go_Popup(); // You can customize this logic as per your requirement
        };
        
        const handleClearButtonClick = (e) => {
            e.stopPropagation();
            handleCloseModal();
        };
        

        return (
            <>
            {showModal && (
                <Background>
                <ModalContainer>
                    <ModalContent onClick={handleModalContentClick}>
                        <ClearButton src={Clear} alt="닫기 버튼" onClick={handleClearButtonClick} />
                        <PopupImage src={first_popup_screen} alt="Modal Image" />
                    </ModalContent>
                </ModalContainer>
                </Background>
            )}
            </>
        );
        }
        export default Modal;